.App {
  text-align: center;
  background: -webkit-gradient(linear, left top, left bottom, color-stop(12%, #0074d9), to(#fafafa));
  background: -webkit-linear-gradient(top, #0074d9 12%, #fafafa 100%);
  background: -o-linear-gradient(top, #0074d9 12%, #fafafa 100%);
  background: linear-gradient(180deg, #0074d9 12%, #fafafa 100%);
  height: 115vh;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

h1 {
  margin: 0;
  color: #fafafa;
  font-size: 48px;
}

.top-spacing {
  height: 100px;
}



